.mestopolozhenie
{
    display: flex;
    gap: 1.25rem;
    @include l-1024
    {
        flex-direction: column;
        gap: 0rem;
        padding-bottom: 2rem;
    }
    .container
    {
        display: flex;
        flex-direction: column;
        padding-right: 0;
        @include l-1024
        {
            padding-left: 1.25rem;
            padding-right: 1.25rem;
        }
        @include xm
        {
            padding-left: 0.75rem;
            padding-right: 0.75rem;
        }
    }
    &__info
    {
        padding-top: 5rem;
        width: calc(50% - 0.625rem);
        @include l-1024
        {
            width: 100%;
            margin-bottom: 2rem;
        }
    }
    &__map
    {
        width: calc(50% - 0.625rem);
        background-color: #d8b997;
        #map
        {
            height: 100%;
            width: 100%;
            transition: opacity .3s ease;
            .ymaps-2-1-79-map-copyrights-promo
            {
                display: none;
            }
        }
        @include l-1024
        {
            width: 100%;
            height: 24.375rem;
            margin-bottom: 2.25rem;
        }
    }
    &__grid
    {
        display: grid;
        margin: auto 0;
        grid-template-columns: 1fr 1fr;
        column-gap: 1.25rem;
        row-gap: 3rem;
        &-mobile
        {
            display: none;
            @include l-1024
            {
                display: block;
            }
        }
        @include l-1024
        {
            padding: 0 2rem;
            column-gap: 2rem;
            row-gap: 2.25rem;
        }
    }
    &__item
    {
        &-title
        {
            @include nums-bold-48;
            color: var(--color-brand-beige);
            margin: -0.75rem 0;
            padding-bottom: 1rem;
        }
        &-desc
        {
            @include body-text-20;
            margin: -0.3rem 0;
            @include l-1024
            {
                @include body-text-mobile-18;
            }
        }
    }
}