.team
{
    &__wrap
    {
        padding-top: 5rem;
        padding-bottom: 2rem;
        @include l-1024
        {
            padding-bottom: 0;
        }
    }
    &__swiper
    {
        margin-top: 3.5rem;
        .big-slider__arrow
        {
            background: rgb(210 210 210 / 20%);
        }
        @include l-1024
        {
            margin-top: 3rem;
        }
    }
    &__card
    {
        display: flex;
        gap: 1.25rem;
        @include l-1024
        {
            flex-direction: column-reverse;
            gap: 1.5rem;
        }
        &-text
        {
            width: 40.375rem;
            min-height: 33.4375rem;
            padding: 9.5625rem 6.9375rem;
            @include l-1024
            {
                width: 100%;
                min-height: auto;
                padding: 0;
                padding-bottom: 2.5rem;
            }
        }
        &-name
        {
            @include h3-32;
            margin-bottom: 1.5rem;
        }
        &-dolzh
        {
            @include body-text-20;
            color: var(--color-brand-beige);
            margin-bottom: 1.5rem;
        }
        &-desc
        {
            @include quote-16;
        }
        &-img
        {
            flex-shrink: 0;
            width: 33.4375rem;
            height: 40.15625rem;
            border-top-left-radius: 100rem;
            border-top-right-radius: 100rem;
            overflow: hidden;
            @include l-1024
            {
                width: 100%;
                height: 32.65rem;
            }
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}