.smi
{
    &__wrap
    {
        padding-top: 5rem;
        padding-bottom: 10rem;
    }
    &__banner
    {
        margin: 3.5rem calc(50% - 50vw);
        height: 20rem;
        line-height: 0;
        @include l-1024
        {
            margin: 2rem calc(50% - 50vw);
        }
        img
        {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__swiper
    {
        margin: 0 calc(50% - 50vw);
        padding-left: 4rem;
        padding-right: 4rem;
        @include l-1024
        {
            padding-left: 1.25rem;
            padding-right: 1.25rem;
        }
        @include xm
        {
            padding-left: 0.75rem;
            padding-right: 0.75rem;
        }
        .swiper-slide
        {
            width: fit-content;
        }
    }
    &__card
    {
        display: flex;
        flex-direction: column;
        width: 19.5rem;
        height: 11.8rem;
        background-color: #F7F7F7;
        border-radius: 0.75rem;
        padding: 1.25rem;
        &-top
        {
            display: flex;
            justify-content: space-between;
            margin-bottom: 1rem;
            @include quote-16;
            color: var(--color-brand-beige);
        }
        &-date
        {
            
        }
        &-author
        {
            
        }
        &-name
        {
            @include bold-18;
        }
        &-btn
        {
            display: flex;
            justify-content: flex-start;
            margin-top: auto;
        }
    }
}