.big-slider
{
    height: 100vh;
    &__item
    {
        position: relative;
        height: 100vh;
        &-img
        {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            &::after
            {
                position: absolute;
                z-index: 2;
                content: "";
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: var(--color-gradient-photo-darker);
                @include l-1024
                {
                    background: var(--color-mobile-gradient-photo-darker);
                }
            }
            img, video
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
                pointer-events: none;
            }
        }
        &-text
        {
            position: relative;
            z-index: 2;
            padding-top: 6rem;
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            color: var(--color-white);
            @include l-1024
            {
                padding-top: 5rem;
                flex-direction: column;
                justify-content: space-between;
                height: calc(100vh - 10rem);
            }
        }
        &-main
        {

        }
        &-subtitle
        {
            @include upper-24;
            margin-bottom: 1.5rem;
            @include l-1024
            {
                @include upper-mobile-20;
                margin-bottom: 1.25rem;
            }
        }
        &-title
        {
            @include h1-96;
            @include l-1024
            {
                @include h1-64;
            }
        }
        &-desc
        {
            font-weight: 400;
            font-style: italic;
            font-size: 24px;
            line-height: 140%;
            width: 34.375rem;
            //text-shadow: 2px 2px 6px #696969, 2px 2px 6px #696969, 2px 2px 6px #696969;
            //background: rgba(0, 0, 0, 0.1);
            //-webkit-backdrop-filter: blur(6px); /* Safari, iOS */
            //backdrop-filter: blur(6px);
            //border-radius: 1rem;
            //padding: 1rem;
            @include l-1024
            {
                @include accent-italic-mobile-20;
                width: 100%;
            }
            b
            {
                @include accent-bold-italic-24;
                color: var(--color-light-beige);
                @include l-1024
                {
                    @include accent-italic-mobile-20;
                }
            }
        }
    }
    &__arrows
    {
        z-index: 2;
        display: flex;
        justify-content: space-between;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: calc(100% - 40px);
        @include l-1024
        {
            display: none;
        }
    }
    &__arrow
    {
        width: 3rem;
        height: 3rem;
        border-radius: 100rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgba(255, 255, 255, .51);
        backdrop-filter: blur(22px);
        opacity: 1;
        transition: opacity .3s ease;
        .icon
        {
            width: 1rem;
            height: 1rem;
        }
        &.swiper-button-disabled
        {
            opacity: 0;
            pointer-events: none;
        }
    }
    .swiper-pagination
    {
        display: flex;
        justify-content: center;
        bottom: 2.125rem;
        @include l-1024
        {
            bottom: auto;
            flex-direction: column;
            align-items: flex-end;
            gap: 0.625rem;
            height: -moz-fit-content;
            height: fit-content;
            width: 0.25rem;
            top: 50%;
            right: 1.25rem;
            left: auto;
            transform: translateY(-50%);
        }
        @include xm
        {
            right: 0.75rem;
        }
    }
    .swiper-pagination-bullet
    {
        width: 2.6875rem;
        height: 0.25rem;
        border-radius: 10rem;
        transition: width .2s ease, height .2s ease,;
        background-color: #FFFFFF;
        opacity: 1;
        @include l-1024
        {
            height: 2.6875rem;
            width: 0.25rem;
        }
        &-active
        {
            width: 7.5rem;
            background-color: #FFFFFF;
            @include l-1024
            {
                height: 7.5rem;
                width: 0.25rem;
            }
        }
    }
}