.partners
{
    &__wrap
    {
        padding-top: 5rem;
        padding-bottom: 5rem;
    }
    &__payments
    {
        margin-top: 6.75rem;
        @include l-1024
        {
            margin-top: 5.875rem;
        }
    }
    &__subtitle
    {
        text-align: center;
        margin-bottom: 4rem;
        h3
        {
            @include h2-40;
            @include l-1024
            {
                @include h3-32;
                text-transform: none;
            }
        }
        @include l-1024
        {
            margin-bottom: 2.5rem;
        }
        &--gold
        {
            color: var(--color-brand-beige);
        }
    }
    &__cards
    {
        @include l-1024
        {
            margin: -1.5rem calc(50% - 50vw);
        }
        .swiper-wrapper
        {
            padding: 1.5rem 0;
            justify-content: center;
            @include l-1024
            {
                justify-content: flex-start;
            }
        }
        .swiper-slide
        {
            width: fit-content;
            &:nth-child(2)
            {
                .partners__card
                {
                    transform: translate(0px, -0.625rem);
                    @include l-1024
                    {
                        transform: translate(0px, 0px);
                    }
                }
            }
        }
    }
    &__card
    {
        display: flex;
        flex-direction: column;
        padding: 2rem 1.5rem;
        padding-bottom: 1.5rem;
        max-width: 16.75rem;
        min-height: 22.75rem;
        border: 0.0625rem solid var(--color-light-beige);
        border-radius: 1.25rem;
        box-shadow: 0.125rem 0.125rem 2rem 0rem #C28C3B12;
        &-title
        {
            text-align: center;
            font-weight: 700;
            font-size: 1.75rem;
            line-height: 2.1875rem;
            padding-bottom: 1rem;
            margin-top: -0.5rem;
            border-bottom: 0.0625rem solid var(--color-light-beige);
        }
        &-text
        {
            margin-top: 1.5rem;
            *
            {
                color: #777777;
                font-weight: 400;
                font-size: 1rem;
                line-height: 1.4rem;
                padding-bottom: 1.25rem;
                margin-bottom: -0.625rem;
            }
            *:last-child
            {
                padding-bottom: 0;
            }
        }
        &-btn
        {
            padding-top: 3rem;
            margin-top: auto;
            .button
            {
                width: 100%;
                text-align: center;            
            }
        }
    }
    &__our
    {
        display: none;
        margin-top: 12rem;
        margin-bottom: 4rem;
        @include l-1024
        {
            margin-top: 4.375rem;
        }
    }
    &__tags
    {
        padding: 1.75rem 0;
        @include l-1024
        {
            margin: 0 calc(50% - 50vw);
        }
        .swiper-slide
        {
            width: fit-content;
        }
        .swiper-wrapper
        {
            justify-content: center;
            @include l-1024
            {
                margin: 0 calc(50% - 50vw);
                justify-content: flex-start;
            }
        }
    }
    &__tag
    {
        display: block;
        width: 11.5rem;
        height: 6.25rem;
        border-radius: 0.75rem;
        overflow: hidden;
        img
        {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    &__next
    {
        //display: flex;
        display: none;
        justify-content: center;
        padding-top: 7.75rem;
        padding-bottom: 4rem;
        &-btn
        {
            display: flex;
            align-items: center;
            padding: 0.625rem;
            gap: 0.625rem;
        }
        &-text
        {
            @include body-text-24;
        }
        &-arrow
        {

        }
    }
}