.radio-checkbox
{
    display: inline-flex;
    align-items: flex-start;
    gap: 0.625rem;
    cursor: pointer;
    user-select: none;
    position: relative;
}

.radio-checkbox input
{
    position: absolute;
    opacity: 0;
    height: 0;
    width: 0;
}

.radio-mark
{
    flex-shrink: 0;
    position: relative;
    height: 1rem;
    width: 1rem;
    background-color: transparent;
    border: 0.0625rem solid var(--color-secondary-gray);
    border-radius: 50%;
    margin-top: 0.2rem;
}

/* Hover effect */
.radio-checkbox:hover input ~ .radio-mark
{
    border-color: var(--color-brand-beige);
}

/* Checked state */
.radio-checkbox input:checked ~ .radio-mark
{
    border: 0.35rem solid var(--color-secondary-gray);
    border-color: var(--color-brand-beige);
}

/* Inner dot */
.radio-mark:after
{
    content: "";
    position: absolute;
    display: none;
    left: 50%;
    top: 50%;
    width: 0.5rem;
    height: 0.5rem;
    background: var(--color-white);
    border-radius: 50%;
    transform: translate(-50%, -50%);
}

/* Show dot when checked */
.radio-checkbox input:checked ~ .radio-mark:after
{
    display: block;
}

/* Disabled state */
.radio-checkbox input:disabled ~ .radio-mark
{
    opacity: 0.5;
    cursor: not-allowed;
}

/* Focus state */
.radio-checkbox input:focus-visible ~ .radio-mark
{
    outline: 0.0625rem solid var(--color-brand-beige);
    outline-offset: 0.0625rem;
}