.gallery
{
    margin-top: 2rem;
    &__date
    {
        @include accent-italic-24;
        color: var(--color-brand-beige);
        margin-top: 1rem;
    }
    &__grid
    {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 16px;
        margin-top: 4rem;
        @include l-1024
        {
            grid-template-columns: 1fr;
        }
    }
    &__item
    {
        height: 15rem;
        overflow: hidden;
        border-radius: 1rem;
        img
        {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}