footer
{
    background-color: var(--color-accent-black);
    color: var(--color-white);
    padding-top: 7.5rem;
    padding-bottom: 5rem;
    @include l-1024
    {
        padding-top: 6.125rem;
        padding-bottom: 4rem;
    }
    .footer
    {
        &__grid
        {
            display: grid;
            row-gap: 5rem;
            column-gap: 8.125rem;
            grid-template-areas: 
            "menu info"
            "logo social";
            @include l-1024
            {
                row-gap: 2rem;
                grid-template-areas: 
                "menu"
                "info"
                "social"
                "logo";
            }
        }
        &__item
        {
            &--menu
            {
                grid-area: menu;
                padding: 0 5rem;
                @include upper-24;
                @include l-1024
                {
                    @include upper-mobile-20;
                    padding: 0 0.75rem;
                    padding-bottom: 4rem;
                }
                ul
                {
                    display: flex;
                    flex-direction: column;
                    gap: 2rem;
                    @include l-1024
                    {
                        gap: 2.25rem;
                    }
                    li
                    {
                        margin: -0.25rem 0;
                        @include l-1024
                        {
                            margin: -0.1875rem 0;
                        }
                    }
                }
            }
            &--info
            {
                grid-area: info;
                display: flex;
                flex-direction: column;
                gap: 4rem;
                @include l-1024
                {
                    gap: 2rem;
                }
                &-item
                {
                    margin: -0.5rem 0;
                    @include l-1024
                    {
                        text-align: center;
                        margin: -0.25rem 0;
                    }
                    &--tel
                    {
                        @include h1-64;
                        @include l-1024
                        {
                            @include h2-mobile-32;
                            margin: -0.5rem 0;
                        }
                    }
                    &--email
                    {
                        @include h2-40;
                        @include l-1024
                        {
                            @include h2-mobile-32;
                            text-transform: lowercase;
                        }
                    }
                    &--time
                    {
                        @include h2-40;
                        @include l-1024
                        {
                            @include h2-mobile-32;
                        }
                    }
                    &--address
                    {
                        @include body-text-24;
                    }
                    &--legal
                    {
                        margin-top: 2rem;
                        @include body-text-20;
                    }
                    &--conf
                    {
                        margin-top: auto;
                        @include upper-24;
                        @include l-1024
                        {
                            @include upper-mobile-20;
                        }
                    }
                }
            }
            &--logo
            {
                grid-area: logo;
                padding: 0 5rem;
                height: 5rem;
                @include l-1024
                {
                    height: 3.375rem;
                }
                img
                {
                    height: 100%;
                    @include l-1024
                    {
                        width: 100%;
                        object-fit: contain;
                    }
                }
            }
            &--social
            {
                grid-area: social;
                display: flex;
                align-items: flex-end;
                line-height: 0;
                gap: 2rem;
                &-icon
                {
                    width: 4rem;
                    height: 4rem;
                    color: var(--color-secondary-gray);
                    @include l-1024
                    {
                        width: 3rem;
                        height: 3rem;
                    }
                }
                @include l-1024
                {
                    justify-content: center;
                    padding: 1.5rem 0;
                }
            }
        }
    }
}
#modal-iframe
{
    @include l-1024
    {
        .hystmodal__close {
            top: -2.5rem;
            right: 0rem;
            background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%23fff' stroke='%23fff' stroke-linecap='square' stroke-miterlimit='50' stroke-width='2' d='M22 2L2 22'/%3E%3Cpath fill='none' stroke='%23fff' stroke-linecap='square' stroke-miterlimit='50' stroke-width='2' d='M2 2l20 20'/%3E%3C/svg%3E");
        }
    }
}
.modal
{
    padding: 2rem;
    background-color: var(--color-white);
    border-radius: 2rem;
    width: 25rem;
    &__iframe
    {
        width: auto;
        border-radius: 0;
        padding: 0;
        line-height: 0;

        iframe
        {
            width: 90vw;
            height: 90vh;
        }
        @include l-1024
        {
            width: auto !important;
            iframe
            {
                width: calc(100vw - 3rem);
                height: 85vh;
            }
        }
    }
    @include l-1024
    {
        width: 22rem;
    }
    &-form
    {
        form
        {
            z-index: 3;
            display: flex;
            flex-direction: column;
            gap: 2.5rem;
            @include l-1024
            {
                width: 100%;
                //padding: 0 1.5rem;
            }
        }
        &__text
        {

        }
        &__title
        {
            text-align: center;
            margin-bottom: 1.5rem;
            h2
            {
                @include h3-32;
                text-transform: none;
            }
        }
        &__desc
        {
            @include body-text-20;
            text-align: center;
            color: var(--color-brand-beige);
        }
        &__fields
        {
            display: flex;
            flex-direction: column;
            gap: 1.5rem;
        }
        &__field
        {
            padding: 0.5rem 0;
            background-color: transparent;
            outline: none;
            border: none;
            border-bottom: 0.0625rem solid var(--color-secondary-gray);
            @include body-text-20;
            &::placeholder
            {
                color: var(--color-secondary-gray);
            }
            @include l-1024
            {
                @include body-text-mobile-18;
                &:-webkit-autofill
                {
                    background-color: transparent !important;
                }
            }
        }
        &__soglasie
        {
            @include quote-16;
            a
            {
                text-decoration: underline;
            }
        }
        &__btn
        {
            display: flex;
            justify-content: center;
            .button
            {
                width: 100%;
                @include body-text-20;
            }
        }
    }
}
.loader-runner
{
    z-index: 10;
    position: fixed;
    top: 100vh;
    left: 0;
    width: 100vw;
    height: 130vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    &-top-to-bottom
    {
        top: 130vh;
    }
    &-bottom-to-top
    {
        top: -130vh;
    }
    &--active-top-to-bottom
    {
        transition: top 1.5s ease;
        top: -130vh;
    }
    &--active-bottom-to-top
    {
        transition: top 1.5s ease;
        top: 130vh;
    }
    &__title
    {
        width: 300px;
        img
        {
            width: 100%;
        }
    }
}