.main-form
{
    position: relative;
    display: flex;
    overflow: hidden;
    justify-content: center;
    background-color: var(--color-accent-black);
    padding-bottom: 3.5rem;
    padding-top: 15.1875rem;
    border-top-left-radius: 100rem;
    border-top-right-radius: 100rem;
    margin-top: 1.875rem;
    @include l-1024
    {
        background-color: transparent;
        padding-bottom: 0;
        padding-top: 4.25rem;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        &::after
        {
            content: "";
            position: absolute;
            z-index: -1;
            width: 60.5rem;
            height: 60.5rem;
            top: 0;
            border-radius: 100rem;
            background-color: var(--color-accent-black);
        }
    }
    form
    {
        z-index: 3;
        display: flex;
        flex-direction: column;
        gap: 2.5rem;
        width: 31.125rem;
        color: var(--color-white);
        @include l-1024
        {
            width: 100%;
            padding: 0 1.5rem;
        }
    }
    &__text
    {

    }
    &__title
    {
        margin: -0.5rem 0;
        margin-bottom: 1rem;
        text-align: center;
        h2
        {
            @include h1-64;
        }
        @include l-1024
        {
            h2
            {
                @include h2-mobile-32;
            }
        }
    }
    &__desc
    {
        @include body-text-20;
        color: var(--color-light-beige);
        text-align: center;
        @include l-1024
        {
            @include body-text-mobile-18;
        }
    }
    &__fields
    {
        display: flex;
        flex-direction: column;
        gap: 1.5rem;
    }
    &__field
    {
        padding: 0.5rem 0;
        background-color: transparent;
        outline: none;
        border: none;
        caret-color: var(--color-white);
        color: var(--color-white);
        border-bottom: 0.0625rem solid var(--color-white);
        @include body-text-20;
        &::placeholder
        {
            color: var(--color-white);
        }
        @include l-1024
        {
            @include body-text-mobile-18;
            &:-webkit-autofill
            {
                background-color: transparent !important;
            }
        }
    }
    &__btn
    {
        display: flex;
        justify-content: center;
        .button
        {
            padding-left: 1.5rem;
            padding-right: 1.5rem;
        }
    }
    &__soglasie
    {
        @include quote-16;
        a
        {
            text-decoration: underline;
        }
    }
}

