header
{
    background-color: var(--color-white);
    position: fixed;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: 10;
    .header
    {
        height: 4rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        & > *
        {
            flex: 1;
        }
        &__burger
        {
            &--opener
            {
                display: block;
            }
            &--closer
            {
                display: none;
            }
            button
            {
                background-color: transparent;
                border: none;
                .icon
                {
                    width: 2rem;
                    height: 2rem;
                    @include l-1024
                    {
                        width: 1.5rem;
                        height: 1.5rem;
                    }
                }
            }
        }
        &__logo
        {
            flex: none;
            text-align: center;
            line-height: 0;
            height: 2.25rem;
            img
            {
                height: 100%;
            }
        }
        &__contacts
        {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 1.5rem;
            &-tel
            {
                @include body-text-20;
                @include l-1024
                {
                    display: none
                }
            }
            &-button
            {
                @include l-1024
                {
                    display: none
                }
            }
        }
        &-window
        {
            display: none;
            padding: 8rem 0;
            height: calc(100vh - 4rem);
            overflow: auto;
            @include l-1024
            {
                padding: 2rem 0;
            }
            &__grid
            {
                display: grid;
                row-gap: 5rem;
                grid-template-areas: 
                "menu info"
                "menu social";
                @include l-1024
                {
                    row-gap: 0rem;
                    grid-template-areas: 
                    "menu"
                    "info"
                    "social";
                }
            }
            &__item
            {
                &--menu
                {
                    grid-area: menu;
                    padding-left: 10.9375rem;
                    padding-right: 5rem;
                    @include upper-24;
                    @include l-1024
                    {
                        @include upper-mobile-20;
                        padding: 0 0.75rem;
                        padding-bottom: 3rem;
                    }
                    ul
                    {
                        display: flex;
                        flex-direction: column;
                        gap: 2rem;
                        @include l-1024
                        {
                            gap: 2.25rem;
                            padding: 0 0.5rem;
                        }
                        li
                        {
                            margin: -0.25rem 0;
                            @include l-1024
                            {
                                margin: -0.1875rem 0;
                            }
                        }
                    }
                }
                &--info
                {
                    grid-area: info;
                    display: flex;
                    flex-direction: column;
                    gap: 1.5rem;
                    @include l-1024
                    {
                        gap: 2rem;
                        padding: 0 1.25rem;
                    }
                    &-item
                    {
                        &--tel
                        {
                            display: flex;
                            flex-direction: column;
                            align-items: flex-start;
                            @include bold-36;
                            @include l-1024
                            {
                                @include h2-mobile-32;
                            }
                            .vyzov
                            {
                                @include link-20;
                                margin-top: 0.25rem;
                                color: var(--color-brand-beige);
                            }
                        }
                        &--email
                        {
                            @include bold-36;
                            @include l-1024
                            {
                                @include h2-mobile-32;
                                text-transform: lowercase;
                            }
                        }
                        &--time
                        {
                            @include bold-36;
                            @include l-1024
                            {
                                @include h2-mobile-32;
                            }
                        }
                        &--address
                        {
                            @include body-text-24;
                        }
                    }
                }
                &--social
                {
                    grid-area: social;
                    display: flex;
                    align-items: flex-start;
                    line-height: 0;
                    gap: 2rem;
                    &-icon
                    {
                        width: 3rem;
                        height: 3rem;
                        color: var(--color-secondary-gray);
                        @include l-1024
                        {
                            width: 3rem;
                            height: 3rem;
                        }
                    }
                    @include l-1024
                    {
                        justify-content: center;
                        padding: 1.5rem 0;
                    }
                }
            }
        }
    }
    &.header--active
    {
        .header__contacts
        {
            visibility: hidden;
        }
        .header-window
        {
            display: block;
        }
        .header__burger--closer
        {
            display: block;
        }
        .header__burger--opener
        {
            display: none;
        }
    }
    .header__menu
    {
        display: none;
    }
}