.invest
{
    &__wrap
    {
        padding-top: 5rem;
        padding-bottom: 12rem;
        @include l-1024
        {
            padding-bottom: 8rem;
        }
    }
    &__tabs
    {
        display: flex;
        gap: 2.25rem;
        margin-top: 4rem;
        margin-bottom: 3rem;
        @include l-1024
        {
            gap: 1.5rem;
            margin-top: 3rem;
            margin-bottom: 2.4rem;
        }
    }
    &__tab
    {
        cursor: pointer;
        color: var(--color-secondary-gray);
        &-name
        {
            @include upper-bold-24;
            padding-bottom: 0.25rem;
            margin-bottom: 0.25rem;
            border-bottom: calc(1rem/16) solid var(--color-secondary-gray);
        }
        &-sq
        {
            @include body-text-20;
        }
        &--active
        {
            color: var(--color-brand-beige);
            .invest__tab-name
            {
                border-bottom: calc(1rem/16) solid var(--color-brand-beige);
            }
        }
    }
    &__table
    {
        @include l-1024
        {
            overflow-x: auto;
        }
        &--active
        {
           display: table !important;
        }
        table
        {
            display: none;
            width: 100%;
            border-spacing: 0;
            @include l-1024
            {
                width: 200%;
            }
            thead
            {
                tr
                {
                    th
                    {
                        @include body-text-20;
                        font-weight: 700;
                        padding: 0;
                        padding-bottom: 1.25rem;
                        border-bottom: calc(1rem/16) solid var(--color-accent-black);
                    }
                }
            }
            tbody
            {
                tr
                {
                    td
                    {
                        padding: 1.25rem 0;
                        border-bottom: calc(1rem/16) solid var(--color-secondary-gray);
                        text-align: center; 
                        &:first-child
                        {
                            width: 19.5rem;
                            text-align: left;
                            @include body-text-20;
                            @include l-1024
                            {
                                @include body-text-mobile-18;
                                width: 10.5rem;
                            }
                        }
                        @include body-text-mobile-18;
                    }
                }
            }
        }
    }
    &__info
    {
        margin-top: 2rem;
    }
}