html
{
    font-size: calc(100vw/95); //16px
    @include l-1024
    {
        font-size: calc(100vw/24.25);//calc(2vw);
    }
    @include xm
    {
        font-size: calc(100vw/24.25); //16px
    }
}
body
{
    background-color: var(--color-white);
    color: var(--color-accent-black);
    font-family: "Arsenal", sans-serif;
    overscroll-behavior-y: contain;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased; /* Сглаживание для WebKit браузеров */
    -moz-osx-font-smoothing: grayscale; /* Сглаживание для Firefox на macOS */
    font-smooth: always;
}
.main
{
    //overflow: hidden;
}
.container
{
    max-width: 100rem;
    padding-left: 4rem;
    padding-right: 4rem;
    margin: 0 auto;
    @include l-1024
    {
        padding-left: 1.25rem;
        padding-right: 1.25rem;
    }
    @include xm
    {
        padding-left: 0.75rem;
        padding-right: 0.75rem;
    }
}
.container--small
{
    padding-left: 17.875rem;
    padding-right: 17.875rem;
    @include l-1024
    {
        padding-left: 2.5rem;
        padding-right: 2.5rem;
    }
    @include xm
    {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }
}
.touchable
{
    padding: 0.5rem;
    margin: -0.5rem;
}
.view
{
    height: 100vh;
    @include l-1024
    {
        height: auto;
    }
    &__title
    {
        h2
        {
            @include h1-64;
        }
        @include l-1024
        {
            h2
            {
                @include h2-mobile-32;
            }
        }
    } 
}
.line--2
{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    white-space: normal;
}
.line--3
{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    white-space: normal;
}
.line--4
{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    overflow: hidden;
    white-space: normal;
}

.hystmodal__window {
    background: transparent;
    width: auto;
}

.hystmodal__close:focus {
    outline: none;
}
.hystmodal__close {
    @include l-1024
    {
        top: 1rem;
        right: 1rem;
    }
}