.button
{
    display: block;
    user-select: none;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 6.25rem;
    color: var(--color-white);
    background-color: var(--color-brand-beige);
    @include button-text-14;
    box-shadow: 0.125rem 0.125rem 0.5rem 0rem #C28C3B59;
    transition: 
    background-color 0.2s ease,
    box-shadow 0.2s ease;
    @include hover
    {
        background-color: #C08E54;
        box-shadow: 2px 2px 8px 0px #C28C3B59;
    }
}