.news
{
    @include l-1024
    {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    &__wrap
    {
        padding-top: 5rem;
        padding-bottom: 2rem;
    }
    &__swiper
    {
        padding-top: 4.125rem;
        padding-bottom: 2.25rem;
        padding-left: 4rem;
        padding-right: 4rem;
        margin: 0 calc(50% - 50vw);
        @include l-1024
        {
            padding-top: 2.125rem;
            padding-bottom: 5.55rem;
            padding-left: 1.25rem;
            padding-right: 1.25rem;
        }
        @include xm
        {
            padding-left: 0.75rem;
            padding-right: 0.75rem;
        }
        .swiper-wrapper
        {
            padding-bottom: 0.5rem;
        }
        .swiper-slide
        {
            width: fit-content;
        }
    }
    &__card
    {
        display: block;
        width: 19.5rem;
        &-img
        {
            background-color: #F4F4F4;
            width: 100%;
            height: 10.875rem;
            border-radius: 0.75rem;
            margin-bottom: 1rem;
            overflow: hidden;
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &-bottom
        {
            display: flex;
            flex-direction: column;
            gap: 0.75rem;
        }
        &-date
        {
            @include quote-16;
            color: var(--color-brand-beige);
        }
        &-name
        {
            @include bold-18;
        }
        &-desc
        {
            position: relative;
            max-height: 5.5rem;
            overflow: hidden;
            @include quote-16;
            &::after
            {
                position: absolute;
                content: "";
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
            }
        }
        &-btn
        {
            display: flex;
            justify-content: center;
        }
    }
    &__showmore
    {
        text-align: center;
        &-btn
        {
            @include link-20;
        }
    }
}