.about-project
{
    display: flex;
    gap: 1.375rem;
    @include l-1024
    {
        flex-direction: column;
    }
    .container
    {
        padding-right: 0;
        @include l-1024
        {
            padding-left: 1.25rem;
            padding-right: 1.25rem;
        }
        @include xm
        {
            padding-left: 0.75rem;
            padding-right: 0.75rem;
        }
    }
    &__description
    {
        flex: 1;
        flex-shrink: 0;
        padding-top: 4rem;
        &-top
        {
            max-width: 33.375rem;
            @include l-1024
            {
                margin-bottom: 2rem;
            }
        }
        &-middle
        {
            @include l-1024
            {
                margin: 0 -1.25rem;
            }
        }
        &-upper-title
        {
            color: var(--color-brand-beige);
            @include upper-24;
            margin-bottom: 1rem;
            span
            {
                margin: -0.5rem 0;
            }
        }
        &-title
        {
            margin-bottom: 1.75rem;
            h2
            {
                @include h2-40;
                margin: -0.125rem 0;
                @include l-1024
                {
                    @include h2-mobile-32;
                }
            }
        }
        &-under-title
        {
            @include body-text-20;
            @include l-1024
            {
                @include body-text-mobile-18;
            }
        }
        &-bottom
        {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-row-gap: 3rem;
            grid-column-gap: 1.25rem;
            padding-top: 7.75rem;
            padding-bottom: 4.75rem;
            @include l-1024
            {
                padding-top: 2rem;
            }
        }
        &-item
        {
            &--top
            {
                font-weight: 400;
                font-size: 1rem;
                text-transform: uppercase;
                line-height: 1.25rem;
                margin: -0.31rem 0;
                padding-bottom: 1rem;
            }
            &--number
            {
                @include nums-bold-48;
                color: var(--color-brand-beige);
                padding-bottom: 1rem;
                margin: -1rem 0;
            }
            &--bottom
            {
                @include body-text-20;
                font-weight: 700;
                color: var(--color-brand-beige);
                margin: -0.4rem 0;
            }
        }
    }
    &__slides
    {
        flex: 1;
        flex-shrink: 0;
        max-width: calc(50% - 1.25rem);
        @include l-1024
        {
            max-width: 100%; 
        }
        &-swiper
        {
            position: sticky;
            top: 0;
            width: 100%;
            height: 100vh;
            @include l-1024
            {
                height: 28.75rem;
            }
            .swiper-wrapper
            {

            }
            .swiper-slide
            {

            }
            .swiper-pagination
            {
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                gap: 0.625rem;
                height: -moz-fit-content;
                height: fit-content;
                width: 0.25rem;
                top: 50%;
                right: 4rem;
                left: auto;
                transform: translateY(-50%);
                @include l-1024
                {
                    right: 1.25rem;
                }
                @include xm
                {
                    right: 0.75rem;
                }
            }
            .swiper-pagination-bullet
            {
                height: 2.6875rem;
                width: 0.25rem;
                border-radius: 10rem;
                transition: height .2s ease;
                background-color: #FFFFFF;
                opacity: 1;
                &-active
                {
                    height: 7.5rem;
                    background-color: #FFFFFF;
                }
            }
        }
    }
    &__image
    {
        width: 100%;
        height: 100%;
        img
        {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}