.banner
{
    width: 100%;
    height: 100vh;
    position: relative;
    &__img
    {
        position: relative;
        line-height: 0;
        width: 100%;
        height: 100%;
        &::after
        {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: var(--color-gradient-photo-darker-180);
            z-index: 1;
        }
        img, video
        {
            height: 100%;
            width: 100%;
            object-fit: cover;
            pointer-events: none;
        }
    }
    &__text
    {
        @include body-text-24;
        p
        {
            margin-top: 1.5rem;
        }
    }
    &__title
    {
        position: absolute;
        z-index: 2;
        bottom: 4rem;
        left: 4rem;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        flex-direction: column;
        width: calc(100% - 8rem);
        gap: 3.75rem;
        align-items: flex-start;
        color: var(--color-white);
        h1
        {
            @include h1-96;
        }
        @include l-1024
        {
            flex-direction: column;
            align-items: flex-start;
            gap: 3rem;
            left: 0.75rem;
            bottom: 7rem;
            width: calc(100% - 1.5rem);
            h1
            {
                @include h1-64;
            }
        }
    }
    button
    {
        align-self: center;
        margin-bottom: 0;
        font-weight: 400;
        font-size: 1.375rem;
        line-height: 1.25em;
        padding: 0.7rem 6rem;
        padding-top: 0.5rem;
        @include l-1024
        {
            width: 100%;
        }
    }
}