.show-more
{
  position: relative;
  &__active
  {
    &::before
    {
      content: unset !important;
    }
  }
  &::before
  {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 33%;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
  }
  &__content
  {
    transition: height .3s ease;
  }
  &__btn
  {
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px;
    position: absolute;
    top: calc(100% + 8px);
    left: 50%;
    transform: translate(-50%,0);
    z-index: 8;
    color: var(--color-brand-beige);
    &-text
    {
      @include body-text-24
    }
    &-icon
    {
      
    }
  }
}

.scroller
{
  position: fixed;
  bottom: -100px;
  transition: all .4s ease;
  z-index: 4;
  left: 50%;
  transform: translate(-50%,0);
  padding-left: 25px;
  padding-right: 20px;
  padding-top: 20px;
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  height: 90px;
  display: flex;
  justify-content: center;
  gap: 8px;
  background-color: #00000040;
  backdrop-filter: blur(12px);
  &--up
  {
    bottom: -10px;
  }
  &--active
  {
    .scroller__page--first
    {
      top: -30px;
      opacity: 0;
    }
    .scroller__page--second
    {
      top: 0px;
      opacity: 1;
    }
    .scroller__page--third
    {
      top: 30px;
      opacity: 0.5;
    }
    .scroller__scroll-item
    {
      top: 16px;
    }
  }
  &__pages
  {
    position: relative;
    width: 48px;
  }
  &__page
  {
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
    width: 48px;
    height: 28px;
    border-radius: 2px;
    background-color: #fff;
    transition: all .4s ease;
    &--first
    {
      top: 0px;
      background-color: #FFFFFF;
    }
    &--second
    {
      top: 30px;
      background-color: #FFFFFF;
      opacity: .5;
    }
    &--third
    {
      top: 60px;
      background-color: #FFFFFF;
      opacity: .25;
    }
  }
  &__scroll
  {
    position: relative;
    display: flex;
    width: 7px;
    height: 56px;
    border-radius: 100px;
    background-color: #FFFFFF80;
    &-item
    {
      position: absolute;
      top: 2px;
      left: 50%;
      transform: translate(-50%,0);
      width: 3px;
      height: 21px;
      border-radius: 5px;
      background-color: #FFFFFF;
      transition: all .4s ease;
    }
  }
}
.mobile-scroller
{
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  z-index: 9;
  transition: all .5s ease;
  &__hand
  {
    position: absolute;
    opacity: 0;
    width: 125px;
    height: 125px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    transition: all .5s ease;
    &--move
    {
      top: 40%;
      opacity: 0;
    }
    img
    {
      width: 100%;
      height: 100%;
    }
  }
}

